<template>
  <a-switch v-bind="comProps" @change="onChange" />
</template>
<script>
export default {
  name: "FieldSwitch",
  props: {
    fieldConfig: {
      type: Object,
      default: () => {},
    },
    fieldVal: {
      type: Object,
      default: () => {},
    },
  },
  data() {
    return { checked: this.fieldVal.value || false };
  },
  computed: {
    comProps: function () {
      // eslint-disable-next-line no-unused-vars
      const { value, ...other } = this.fieldVal || {};
      return {
        size: "small",
        ...other,
        checked: this.checked,
      };
    },
  },
  // ÊñπÊ≥ïÈõÜÂêà
  methods: {
    // Â§ÑÁêÜSwitch
    onChange(value) {
      this.checked = value;
      if (this.$listeners.setValue) this.$emit("setValue", { value });
    },
  },
};
</script>
