<template>
  <ConfigProvider :locale="locale">
    <div id="app">
      <div class="tool-bar">
        <div class="logo">
          <div class="logo-icon">üß©</div>
          <span class="logo-text">Flow Editor</span>
        </div>
        <div class="controls">
          <button class="control-btn" id="btn-clear" @click="clearCanvas">
            Ê∏ÖÁ©∫
          </button>
          <button
            class="control-btn"
            id="btn-config"
            @click="settingVisible = true"
          >
            ËÆæÁΩÆ
          </button>
          <button class="control-btn" id="btn-save" @click="saveData">
            ‰øùÂ≠ò
          </button>
        </div>
      </div>
      <ProcessEditor
        ref="editor"
        class="process-synergy"
        :processData="processData"
        :tabConfig="tabConfig"
        :editorConfig="editorConfig"
        :customNodes="customNodes"
        :customFields="customFields"
        :staticDataSource="staticDataSource"
      />
      <a-modal
        v-model="showEditor"
        :bodyStyle="{ height: '450px', overflow: 'auto' }"
        width="80%"
        title="JsonÊï∞ÊçÆ"
        :footer="null"
      >
        <pre>{{ jsonData }}</pre>
      </a-modal>
      <CommonDragDrawer
        width="550px"
        minWidth="550px"
        :title="'Â±ûÊÄßÈÖçÁΩÆ'"
        :visible.sync="settingVisible"
        @close="onDrawerClose"
      >
        <template slot="footer">
          <a-button @click="onDrawerClose" style="margin-right: 0.5rem">{{
            "ÂèñÊ∂à"
          }}</a-button>
          <a-button type="primary" @click="onDrawerOk">{{ "Á°ÆÂÆö" }}</a-button>
        </template>
      </CommonDragDrawer>
    </div>
  </ConfigProvider>
</template>

<script>
import { ConfigProvider, message } from "ant-design-vue";
import zhCN from "ant-design-vue/es/locale/zh_CN"; // ÂºïÂÖ•‰∏≠ÊñáËØ≠Ë®ÄÂåÖ
// import zhCN from "ant-design-vue/lib/locale-provider/zh_CN";
import processData from "./mock/data.json";
import staticDataSource from "./mock/staticDataSource.json";
import tabConfig from "./mock/tabConfig.js";
import customDataV from "@/components/customNodes/customDataV.vue";
import customNode from "@/components/customNodes/customHtml.vue";
import FieldSwitch from "./components/customFields/field-switch.vue";

function getPortType(locatorName) {
  let portType = "bottom";
  switch (locatorName) {
    case "draw2d.layout.locator.TopLocator":
      portType = "top";
      break;
    case "draw2d.layout.locator.BottomLocator":
      portType = "bottom";
      break;
    case "draw2d.layout.locator.LeftLocator":
      portType = "left";
      break;
    case "draw2d.layout.locator.RightLocator":
      portType = "right";
      break;
  }
  return portType;
}

export default {
  name: "App",
  components: { ConfigProvider },
  data() {
    return {
      locale: zhCN,
      processData,
      staticDataSource,
      tabConfig,
      jsonData: "",
      showEditor: false,
      settingVisible: false,
    };
  },
  computed: {
    editorConfig() {
      return {
        // panelType: "collapse",
      };
    },
    customNodes() {
      return [
        {
          type: "HtmlTask",
          nodeType: "CustomTask",
          text: "Ëá™ÂÆö‰πâÂõæË°®",
          component: customNode,
          attr: { width: 400, height: 250, resizeable: true },
        },
        {
          type: "HtmlTask",
          nodeType: "DataVTask",
          text: "Ëá™ÂÆö‰πâTable",
          component: customDataV,
          attr: { width: 450, height: 220 },
        },
      ];
    },
    customFields() {
      return {
        SWITCH: {
          component: FieldSwitch,
          props: {},
          events: {},
        },
      };
    },
  },
  mounted() {
    // Ê≥®ÂÜåÈªòËÆ§ÁîüÂëΩÂë®ÊúüÈí©Â≠ê
    this._registerDefaultHooks();

    this.$refs.editor.designer.init().then(() => {
      this.$refs.editor.designer.importProcess(this.processData);
      this.$refs.editor.initCanvas(this.$refs.editor.designer.canvas);
    });
  },
  methods: {
    // Ê≥®ÂÜåÈªòËÆ§Ê†°È™åÂíåÂõûË∞ÉÁ§∫‰æã
    _registerDefaultHooks() {
      // ÈòªÊ≠¢Âà†Èô§ÁâπÊÆäËäÇÁÇπÁ§∫‰æã
      this.$refs.editor.designer.on(
        "beforeRemoveNode",
        ({ event: { figure } } = {}) => {
          if (figure.protected) {
            message.warning("Á¶ÅÊ≠¢Âà†Èô§");
            return false; // ÈòªÊ≠¢Âà†Èô§
          }
        },
        { blockAble: true }
      );

      this.$refs.editor.designer.on("afterAddNode", ({ emitter, event }) => {
        if (
          event.figure &&
          ["start", "end"].includes(event.figure.nodeType.toLowerCase())
        )
          event.figure.protected = true;
      });
    },
    customContextMenu(menuConfig) {},
    clearCanvas() {
      const designer = this.$refs.editor.designer;
      if (designer) {
        designer.clear();
        this.$refs.editor.setTemplateProperty();
      }
    },
    onDrawerClose() {
      this.settingVisible = false;
    },
    onDrawerOk() {
      this.onDrawerClose();
    },
    saveData() {
      const canvas = this.$refs.editor.designer.canvas;
      let figures = canvas.getFigures().data || [];
      let lines = canvas.getLines().data || [];
      const res = {
        template: this.$refs.editor.templateJson,
        bizFigures: figures
          .filter((o) => o.nodeType)
          .map((figure) => ({
            id: figure.id,
            attr: {
              type: figure.type,
              nodeType: figure.nodeType,
              x: figure.getX(),
              y: figure.getY(),
            },
            userData: figure.userData,
          })),
        noneBizFigures: figures
          .filter((o) => !o.nodeType)
          .map((figure) => ({
            id: figure.id,
            attr: {
              type: figure.type,
              x: figure.getX(),
              y: figure.getY(),
            },
            userData: figure.userData,
          })),
        lines: lines.map((line) => {
          const source = line.sourcePort || {};
          const target = line.targetPort || {};
          return {
            id: line.id,
            attr: {},
            source: {
              id: source.parent.id,
              portType: getPortType(source.locator.NAME),
            },
            target: {
              id: target.parent.id,
              portType: getPortType(target.locator.NAME),
            },
            userData: line.userData,
          };
        }),
      };
      console.log("res", res);
      // console.log("res", JSON.stringify(res));
      this.jsonData = res;
      // this.jsonData = JSON.stringify(res);
      this.showEditor = true;
    },
  },
};
</script>

